import { Component, ViewChild } from '@angular/core';
import { Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HomePage } from '../pages/home/home';
import { PerfilPage } from '../pages/perfil/perfil';
import { SobrePage } from '../pages/sobre/sobre';
import { AuthProvider } from '../providers/auth/auth';
import { LoginCadastroPage } from '../pages/login-cadastro/login-cadastro';
import { AlertController } from 'ionic-angular';

import { OneSignal, OSNotificationPayload } from '@ionic-native/onesignal';
import { isCordovaAvailable } from '../common/is-cordova-available';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any;

  pages: Array<{title: string, component: any}>;

  constructor(public platform: Platform, 
    public statusBar: StatusBar, 
    public splashScreen: SplashScreen,
    public autenticacao: AuthProvider,
    public oneSignal: OneSignal,
    public alertCtrl: AlertController) {

    // used for an example of ngFor and navigation
    this.pages = [
      { title: 'Home', component: HomePage },
      { title: 'Perfil', component: PerfilPage },
      { title: 'Sobre', component: SobrePage }
    ];

    this.platform.ready().then(() => {
      this.autenticacao.retornaUserObservable().subscribe(
        user => {
          if (user) {
            this.rootPage = HomePage;
          } else {
            this.rootPage = LoginCadastroPage;
          }
        },
        () => {
          this.rootPage = LoginCadastroPage;
        }
       );
        statusBar.backgroundColorByHexString("#006400");
        splashScreen.hide();

        let alert = this.alertCtrl.create({
              title: 'test',
              subTitle: 'test'
            });
  alert.present();

      if (isCordovaAvailable()){
        this.oneSignal.startInit('d0ce017c-912b-4859-8ae2-b707856f87de', '580258323760');
        this.oneSignal.inFocusDisplaying(this.oneSignal.OSInFocusDisplayOption.Notification);
        this.oneSignal.handleNotificationReceived().subscribe(data => this.onPushReceived(data.payload));
        this.oneSignal.handleNotificationOpened().subscribe(data => this.onPushOpened(data.notification.payload));
        this.oneSignal.endInit();

        this.oneSignal.getIds().then(result => {
            
        }, error => {
          
           
        })


      }
      

    });


  }


  private onPushReceived(payload: OSNotificationPayload) {
    alert('Push recevied:' + payload.body);
  }

  private onPushOpened(payload: OSNotificationPayload) {
    alert('Push opened: ' + payload.body);
  }



  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }
}



