import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { AuthProvider } from '../../providers/auth/auth';
import { EditaPerfilPage } from '../edita-perfil/edita-perfil';
import { Usuario } from '../../models/usuario';
import { DividaServiceProvider } from '../../providers/divida-service/divida-service';
import { EmprestimoServiceProvider } from '../../providers/emprestimo-service/emprestimo-service';
import { LoginCadastroPage } from '../login-cadastro/login-cadastro';

@Component({
  selector: 'page-perfil',
  templateUrl: 'perfil.html'
})
export class PerfilPage {
  public usuario = {} as Usuario;
  foto;
 
  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public authService: AuthProvider,
    public dividaService: DividaServiceProvider,
    public emprestimoService: EmprestimoServiceProvider
  ) {
    authService.getUsuario().subscribe(res =>{
      console.log(res);
      this.usuario = res as Usuario;
    });
  }

  existeFoto() {
    if(this.usuario != null) {
      if(this.usuario.fotosrc != null) {
        return true;
      }
    } else {
      return false;
    }
  }


  editaPerfil(){
    this.navCtrl.push(EditaPerfilPage, this.usuario);
  }

  logout() {
    this.authService.logout();
    this.navCtrl.setRoot(LoginCadastroPage);
  }

  formataValor(valor: number) {
    return Intl.NumberFormat("pt-BR", { minimumFractionDigits: 2, maximumFractionDigits: 2 }).format(valor);
  }

  somaDividas(){
    return this.formataValor(this.dividaService.retornaSomaDividas());
  }

  somaEmprestimos(){
    return this.formataValor(this.emprestimoService.somaEmprestimos());
  }
}



