import { Injectable } from '@angular/core';
import { Divida } from "../../models/divida";


@Injectable()
export class DividaServiceProvider {

  private dividas: Divida[] = [];

  constructor() {
    console.log('Hello DividaServiceProvider Provider');
  }

  public adiciona(divida: Divida) {
    this.dividas.push(divida);
    console.log(this.dividas);
  }

  public recebeDividas() {
    return this.dividas;
  }

  public removeDivida(divida: Divida) {
    var index = this.dividas.indexOf(divida);
    this.dividas.splice(index, 1);
  }

  public editarDivida(divida: Divida) {
    //edita a divida no firebase
  }

  public retornaSomaDividas() {
    var soma = 0.0;
    for(var i = 0; i < this.dividas.length; i++) {
      soma += +this.dividas[i].valor;
    }
    return soma;
  }

}



