import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, ActionSheetController } from 'ionic-angular';
import { Usuario } from '../../models/usuario';
import { AuthProvider } from '../../providers/auth/auth';
import { PhotoServiceProvider } from '../../providers/photo-service/photo-service';

/**
 * Generated class for the EditaPerfilPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-edita-perfil',
  templateUrl: 'edita-perfil.html',
})
export class EditaPerfilPage {
  public usuario = {} as Usuario;

  nomeVazio = false;
  novaFoto: string;
  existeFoto: boolean;
  fotoExcluida: boolean;

  constructor(
    public navCtrl: NavController, 
    public authProvider: AuthProvider,
    public navParams: NavParams,
    public actionSheetCtrl: ActionSheetController,
    public photoServiceProvider: PhotoServiceProvider,
    public toastController: ToastController
  ) {
    
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EditaPerfilPage');
  }

  ionViewWillEnter() {
    this.usuario = this.navParams.data;
    this.verificaFoto();
  }

  editaUsuario(usuario: Usuario) {
    if (!this.confirmaDados()) {
      this.abrirToast("Ops! Parece que você deixou de preencher algo, ou preencheu incorretamente.");
    } else {
      this.abrirToast("Salvando...");
      if(this.novaFoto != null) {
        this.photoServiceProvider.realizaUpload(this.novaFoto).then(result => {
        }, error => {
          this.abrirToast("Erro: " + error);
        });
      }
      if(this.fotoExcluida) {
        this.authProvider.excluiFoto();
      }
      this.authProvider.salvarNomeUsuario(this.usuario.nome).then(result => {
        this.navCtrl.pop();
        this.abrirToast("Dados salvos.");
      })
    }
  }

  verificaFoto() {
    if(this.usuario != null) {
      if(this.usuario.fotosrc != null) {
        this.existeFoto = true;
      }
    } else {
      this.existeFoto = false;
    }
  }

  mudarFoto(): void {
    let actionSheet = this.actionSheetCtrl.create({
      enableBackdropDismiss: true,
      buttons: [
        {
          text: 'Tirar foto',
          icon: 'camera',
          handler: () => {
            this.photoServiceProvider.retornaFoto("Camera").then(result => {
              this.novaFoto = result;
            });
          }
        }, {
          text: 'Selecionar da galeria',
          icon: 'images',
          handler: () => {
            this.photoServiceProvider.retornaFoto("Galeria").then(result => {
              this.novaFoto = result;
            });
          }
        }
      ]
    });
    actionSheet.present();
  }

  excluiFoto() {
    this.existeFoto = false;
    this.fotoExcluida = true;
  }

  private confirmaDados() {
    this.nomeVazio = this.usuario.nome == "" || this.usuario.nome == null;
    return !this.nomeVazio;
  }

  private abrirToast(text) {
    let toast = this.toastController.create({
      message: text,
      duration: 2000,
      position: 'bottom'
    });
    toast.present();
  }

  

}



