import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, AlertController } from 'ionic-angular';
import { Emprestimo } from '../../models/emprestimo';
import { EmprestimoServiceProvider } from '../../providers/emprestimo-service/emprestimo-service';
import { EditaEmprestimoPage } from '../edita-emprestimo/edita-emprestimo';
import { StatusBar } from '@ionic-native/status-bar';




@IonicPage()
@Component({
  selector: 'page-mostra-emprestimo',
  templateUrl: 'mostra-emprestimo.html',
})
export class MostraEmprestimoPage {
  public emprestimo = {} as Emprestimo;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public emprestimoService: EmprestimoServiceProvider,
    public toastController: ToastController,
    public alertController: AlertController,
    public statusBar: StatusBar
  ) {
    this.emprestimo = this.navParams.data;
  }

  ionViewWillEnter() {
    this.statusBar.backgroundColorByHexString("#00006b");
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MostraEmprestimoPage');
  }

  formataValor(valor: number) {
    return Intl.NumberFormat("pt-BR", { minimumFractionDigits: 2, maximumFractionDigits: 2 }).format(valor);
  }

  fechaEmprestimo(emprestimo: Emprestimo){
    this.emprestimo.aberta = false;
    this.navCtrl.pop();
    this.abreToast("Empréstimo concluído.");
  }

  private abreToast(text) {
    let toast = this.toastController.create({
      message: text,
      duration: 2000,
      position: 'bottom'
    });
    toast.present();
  }

  deletaEmprestimo(emprestimo: Emprestimo) {
    let alert = this.alertController.create({
      title: "Excluir empréstimo",
      message: "Você tem certeza que deseja excluir o empréstimo para " + emprestimo.nome + "?"
        + "\n\n" + "Todas as informações serão deletadas",
      buttons: [{
        text: 'Cancelar',
        handler: () => {
          console.log('Clicou em cancelar');
        }
      },
      {
        text: 'Excluir',
        handler: () => {
          this.emprestimoService.removeEmprestimo(emprestimo);
          this.navCtrl.pop();
          this.abreToast("Empréstimo removido.");
        }
      }

      ]
    });
    alert.present();
}

modalEditaEmprestimo(emprestimo: Emprestimo){
  this.navCtrl.push(EditaEmprestimoPage, emprestimo);
}

getTextoData() {
  var options = { weekday: "long", month: 'long', day: '2-digit' };
  return this.emprestimo.data.toLocaleDateString("pt-br", options);
}


}



